.card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 1rem;
  padding: 1rem;
  border: 1px solid #222;
  border-radius: 1rem;
  background-color: #3b3b3b;

  &:hover {
    background-color: #303030;
    cursor: pointer;
  }
}

.card-name {
  font-weight: 900;
}

p {
  margin: 0.2rem 0;
}

.monster-img {
  margin-bottom: 1rem;
}
